@import url(https://fonts.googleapis.com/css?family=Cinzel|Oxanium&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    background-color: rgb(215, 236, 243);
    margin: 30px;
}

button {
text-align: center;
}

img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 300px;
    height: 300px;

}

h1 {
    text-align: center;
    font-family: 'Cinzel', serif;
    color: darkblue;
    font-weight: 800;
    text-decoration: underline;
}

h2 {
    text-align: center;
    font-family: 'Oxanium', cursive;
    color: darkblue;
}

button {
    position: absolute; 
    top: 80%;
    left: 48%;
    transform: translate(-50%, -50%); 
    width: 150px;
    padding: 10px;
    font-family: 'Cinzel', serif;
    font-size: 25px;
    text-align: center;
    font-weight: 800;
    text-decoration: none;
    background-color: #3041d4;
    color: white;
    border-radius: 15px;
  }

